<template>
  <ul class="pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" href="" aria-label="Предыдущая страница" @click.prevent="previousPage">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pageNumbers" :key="pageNumber">
      <a :class="['pagination__link', {'pagination__link--current': pageNumber === currentPage}]" href="" @click.prevent="changePage(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" href="" aria-label="Следующая страница" @click.prevent="nextPage">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
model:{
prop:"page",
event:'changePage',
},
  name: 'BasePagination',
  props: {
    page: {
      type: Number,
      required: true
    },
    count: {
      type: Number,
      required: true
    },
    perPage: {
      type: Number,
      required: true
    }
  },
  computed: {
    currentPage() {
      return this.page;
    },

    totalPages() {
  return Math.ceil(this.count / this.perPage);
},


    pageNumbers() {
      const numbers = [];
      for (let i = 1; i <= this.totalPages; i++) {
        numbers.push(i);
      }
      return numbers;
    }
  },
  methods: {
    changePage(pageNumber) {
      this.$emit('changePage', pageNumber);
    },
    previousPage() {
      if (this.currentPage > 1) {
        this.changePage(this.currentPage - 1);
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.changePage(this.currentPage + 1);
      }
    }
  }
};
</script>
