<template>
<aside class="filter">
        <h2 class="filter__title">Фильтры</h2>

        <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
          <fieldset class="form__block">
            <legend class="form__legend">Цена</legend>
            <label class="form__label form__label--price">
              <input class="form__input" type="text" name="min-price" value="0" v-model.number ="currentPriceFrom">
              <span class="form__value">От</span>
            </label>
            <label class="form__label form__label--price">
              <input class="form__input" type="text" name="max-price" v-model.number = "currentPriceTo">
              <span class="form__value">До</span>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Категория</legend>
            <label class="form__label form__label--select">
              <select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
                <option value="value1">Все категории</option>
                <option :value="parseInt(category.id)" v-for="category in categories" :key="category.id">{{ category.title }}</option>
              </select>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Цвет</legend>
            <ul class="colors">

    <li class="colors__item" v-for="(color, index) in dataColors" :key="index">

      <label class="colors__label" :for="color.id">

        <input class="colors__radio sr-only" type="radio" name="color" :id="color.id" :value="color.id" v-model="currentColor">

        <span class="colors__value" :style="{ backgroundColor: color.color }"></span>

      </label>

    </li>

  </ul>
</fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Объемб в ГБ</legend>
            <ul class="check-list">
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked="">
                  <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
                  <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
                  <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
                  <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="128">
                  <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="264">
                  <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <button class="filter__submit button button--primery" type="submit">
            Применить
          </button>
          <button class="filter__reset button button--second" type="button" @click.prevent="resetFilter">
            Сбросить
          </button>
        </form>
      </aside>
</template>

<script>
import categories from '@/data/categories';
import dataColors from '@/data/colors'
import axios from 'axios';
import {API_BASE_URL} from '../config';
export default{
data(){
return{
currentPriceFrom: 0,
currentPriceTo: 0,
currentCategoryId: 0,
dataColors: dataColors,
currentColor: '',
categoriesData: null,
}
},

props: ['filterPriceFrom','filterPriceTo','filterCategoryId', 'filterColorId'],
computed:{
categories(){
return this.categoriesData ? this.categoriesData.items:[];
}
},


watch:{
  filterPriceFrom(value){
     this.currentPriceFrom = value;
  },

  filterPriceTo(value){
     this.currentPriceTo = value;
  },

  filterCategoryId(value){
     this.currentCategoryId = value;
  },

filterColorId(newValue) {
    this.currentColor = newValue;
  }

  },


methods:{
  submit(){
  this.$emit('update:filterPriceFrom', this.currentPriceFrom);
  this.$emit('update:filterPriceTo', this.currentPriceTo);
  this.$emit('update:filterCategoryId', this.currentCategoryId);
  this.$emit('update:filterColorId', this.currentColor);
  },
  resetFilter() {
  this.$emit('update:filterPriceFrom', 0);
  this.$emit('update:filterPriceTo', 0);
  this.$emit('update:filterCategoryId', 0);
  this.$emit('update:filterColorId', 0);
},

loadCategories(){
axios.get(API_BASE_URL + `/api/productCategories`)
.then(Response => this.categoriesData = Response.data);
}
},
created(){
this.loadCategories()
}

}


</script>
