<template>
<main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{countProducts}} товаров
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :categories="categories" :filterPriceFrom.sync="filterPriceFrom" :filterPriceTo.sync="filterPriceTo" :filterCategoryId.sync="filterCategoryId" :filterColorId.sync="filterColorId"></ProductFilter>
      <section class="catalog">
           <div v-if="productsLoading">Загрузка товаров...</div>
           <div v-if="productsLoading">Произошла ошибка при загрузке товаров <button @click.prevent="loadproduct">Повторить загрузку</button></div>
        <ProductList :products="displayedProducts"></ProductList>
        <BasePagination v-model="page" :count="parseInt(countProducts)" :per-page="productsPerPage" v-if="countProducts"></BasePagination>

      </section>

    </div>
  </main>

</template>

<script>
import ProductFilter from '@/components/ProductFilter';
import ProductList from '@/components/ProductList'
import BasePagination from '@/components/BasePagination'
import axios from 'axios';
import { API_BASE_URL } from '@/config';
export default {
  components: {ProductFilter, ProductList, BasePagination },
  data() {
    return {
      categories: [],
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFalled: false,
    };
  },
  computed: {

    filteredProducts(){
       let filteredProducts = this.productsData ? Object.values(this.productsData.items.map(product =>{
      return{
      ...product,
      image: product.image.file.url
      }
      })) : [];

      return filteredProducts;
    },
    displayedProducts() {

      return this.filteredProducts;
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }

  },
  methods:{
    loadProducts(){
      this.productsLoading = true;
       this.productsLoadingFalled = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout (() =>{
        axios.get(API_BASE_URL + '/api/products', {
      params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
            }
      })
      .then(response => this.productsData = response.data)
      .then(()=> this.productsLoadingFalled = true )
      .then(()=> this.productsLoading = false );

      }, 0);
    }
  },

watch: {
  page() {
    this.loadProducts();
  },
  filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    }
},

  created(){
    this.loadProducts();
  }

}

</script>
