<template>
  <div>
    <router-link class="catalog__pic" :to = "{name:'product', params:{id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="(color, index) in filteredColors" :key="index">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :name="'color-' + index" :value="color.color" v-model="selectedColor">
          <span class="colors__value" :style="{ backgroundColor: color.color }">
          </span>
        </label>
      </li>
    </ul>
  </div>
</template>

<script>
import dataColors from '../data/colors';
import gotoPage from '@/helpers/gotoPage'
import numberFormat from '@/helpers/numberFormat';

export default {
  props: {
    product: {
      type: Object,
      required: true
    }
  },

 methods:{
 gotoPage
 },

  data() {
    return {
      selectedColor: '',
      colors: dataColors
    }
  },
  computed: {
    filteredColors() {
      return this.colors.filter(color => color.id === 1 || color.id === 2 || color.id === 3);
    }
  },

  filters:{
  numberFormat
  },
  watch: {
    selectedColor(newVal) {
      this.$emit('update-color', newVal);
    }
  }
}
</script>
